@import url("https://fonts.googleapis.com/css2?family=DM+Sans:wght@400;500;700&display=swap");
body {
  margin: 0;
  line-height: normal;
}
:root {
  /* fonts */
  --xs-medium: "DM Sans";

  /* font sizes */
  --xs-medium-size: 1.25rem;
  --font-size-9xl: 1.75rem;
  --font-size-sm: 0.88rem;
  --font-size-5xl: 1.5rem;

  /* Colors */
  --offwhite: #fafafa;
  --offblack: #151515;
  --white: #fff;
  --color-darkgray-100: #9f9f9f;
  --grey3: #9b9b9b;
  --grey1: #efefef;
  --color-black: #000;
  --grey2: #c8c8c8;

  /* Gaps */
  --gap-5xl: 1.5rem;
  --gap-5xs: 0.5rem;

  /* Paddings */
  --padding-xs: 0.75rem;
  --padding-5xl: 1.5rem;

  /* Border radiuses */
  --br-45xl: 64px;
  --br-5xs: 8px;
  --br-sm: 14px;
}
